<template>
    <div id="post" style="margin-top:10px" v-if="PostList">
        <LoadRefresh @refresh="refreshEmit()" @load="loadingEmit()">
            <div :key="item.pid" v-for="item in PostList" class="post_item">
                <div style="padding: 0px 10px;">
                    <div class="user_info">
                        <img v-lazy='(item.avatar) ? (url + item.avatar) : require("../assets/icon.png")'
                            alt="user_icon" class="user_icon">
                        <div style="padding-top: 2px">
                            <div style="display:flex;text-align: center;align-items: center;">
                                <div style="margin-right:5px">{{ item.nickname }}</div>
                                <img :src="require('../assets/Level/Level' + item.level + '.png')" alt="level"
                                    style="width:20px;height: 20px;">
                            </div>
                            <div style="display:flex">
                                <div class="time">
                                    {{ ComputedTime(item.postTime) }}
                                </div>
                                <div class="forum">
                                    {{ item.forumName }}
                                </div>
                            </div>
                        </div>
                    </div>
                    <div>
                        <a @click="goToPost(item.pid, item.fid)">
                            <div class="title">
                                {{ item.title }}
                            </div>
                            <img v-lazy='url + item.picture' alt="post_img" class="post_img" v-if="item.picture">
                        </a>
                        <div class="button" v-if="!this.$route.params.uid">
                            <!-- <div style="display: flex" @click="collect(item)">
                                <img src="../assets/ButtonUI/Star.png" alt="star_button"
                                    style="width:20px;padding-right: 5px;">
                                <div style="width: 0px; color:gray">{{ 0 }}</div>
                            </div> -->
                            <a @click="goToPost(item.pid)" style="display: flex">
                                <img src="../assets/ButtonUI/ReviewButton.png" alt="message"
                                    style="width:20px;padding-right: 5px;">
                                <div style="width: 0px; color:gray">{{ item.replyNum }}</div>
                            </a>
                            <div style="display:flex" @click="handleLike(item)">
                                <img :src="(item.isLike) ? require('../assets/ButtonUI/LikeButtonOn.png') : require('../assets/ButtonUI/LikeButton.png')"
                                    alt="star" style="width:20px;padding-right: 5px;">
                                <div style="width: 0px; color:gray">{{ item.likeNum }}</div>
                            </div>
                        </div>
                    </div>
                </div>
                <hr class="line">
            </div>
        </LoadRefresh>
    </div>
</template>

<script>
import LoadRefresh from '@/components/LoadRefresh.vue'
import { GetPosts } from '@/api/UserApi'
import config from '@/api/config'
import { LikePost, CancelLikePost, GetPostsByRelated } from '@/api/PostApi'
import { GetForumPosts } from "@/api/ForumApi"
export default {
    name: "PostPage",
    data() {
        return {
            PostList: null,
            url: config.url,
            user: config.user
        };
    },
    components: { LoadRefresh },
    mounted() {
        this.getInitData()
        document.querySelector('body').setAttribute('style', 'margin:0')
    },
    unmounted() {
        document.body.removeAttribute('style')
    },
    methods: {
        ComputedTime(time) {
            let dateBegin = new Date(time.replace(/-/g, '/'))
            let dateEnd = new Date()
            let dateDiff = dateEnd.getTime() - dateBegin.getTime();//时间差的毫秒数
            let dayDiff = Math.floor(dateDiff / (24 * 3600 * 1000));//计算出相差天数
            if (dayDiff > 7) {
                return time.split(' ')[0]
            }
            if (dayDiff > 0) {
                return dayDiff + '天前'
            }
            let hourDiff = Math.floor(dateDiff / (3600 * 1000))//计算出相差小时数
            if (hourDiff > 0) {
                return hourDiff + '小时前'
            }
            let minutesDiff = Math.floor(dateDiff / (60 * 1000))//计算出相差分钟数
            if (minutesDiff > 0) {
                return minutesDiff + '分钟前'
            }
            let secondsDiff = Math.floor(dateDiff / 1000)//计算出相差秒数
            if (secondsDiff > 0) {
                return secondsDiff + '秒前'
            }
        },
        goToPost(pid, fid) {
            window.jsAdapter.goToPost(pid, fid)
        },
        async handleLike(item) {
            (!item.isLike) ?
                LikePost({
                    uid: this.user.uid,
                    pid: item.pid,
                }, {
                    token: this.user.token
                }).then((response) => {
                    console.log(response)
                    item.isLike = true
                    item.likeNum++
                })
                :
                CancelLikePost({
                    uid: this.user.uid,
                    pid: item.pid,
                }, {
                    token: this.user.token
                }).then((response) => {
                    console.log(response)
                    item.isLike = false
                    item.likeNum--
                })
        },
        async refreshEmit() {
            return await this.getInitData()
        },
        async loadingEmit() {
            return await this.getLoadData()
        },
        async getInitData() {
            if (this.$route.params.related_uid) {
                GetPostsByRelated({
                    uid: this.user.uid,
                    offset: 0,
                    pageSize: 10
                }, {
                    token: this.user.token
                }).then((response) => {
                    this.PostList = response.data.data
                }).catch(err => console.log(err))
            } else {
                (this.$route.params.fid) ?
                    GetForumPosts({
                        uid: this.user.uid,
                        fid: this.$route.params.fid,
                        offset: 0,
                        pageSize: 10,
                        order: 1,
                    }, {
                        token: this.user.token
                    }).then((response) => {
                        this.PostList = response.data.data
                    }).catch(err => console.log(err))
                    :
                    GetPosts({
                        pageSize: 10,
                        offset: 0,
                        order: (this.$route.params.uid) ? 1 : 3,
                        uid: (this.$route.params.uid) ? this.$route.params.uid : this.user.uid
                    }, {
                        token: this.user.token
                    }).then((response) => {
                        this.PostList = response.data.data
                    }).catch(err => console.log(err))
            }
        },
        async getLoadData() {
            if (this.$route.params.related_uid) {
                GetPostsByRelated({
                    uid: this.user.uid,
                    offset: 0,
                    pageSize: this.PostList.length
                }, {
                    token: this.user.token
                }).then((response) => {
                    this.PostList.push.apply(this.PostList, response.data.data)
                }).catch(err => console.log(err))
            } else {
                (this.$route.params.fid) ?
                    GetForumPosts({
                        uid: this.user.uid,
                        fid: this.$route.params.fid,
                        offset: this.PostList.length,
                        pageSize: 10,
                        order: 1,
                    }, {
                        token: this.user.token
                    }).then((response) => {
                        this.PostList.push.apply(this.PostList, response.data.data)
                    }).catch(err => console.log(err))
                    :
                    GetPosts({
                        pageSize: 10,
                        offset: this.PostList.length,
                        order: (this.$route.params.uid) ? 1 : 3,
                        uid: (this.$route.params.uid) ? this.$route.params.uid : this.user.uid
                    }, {
                        token: this.user.token
                    }).then((response) => {
                        if (response.data.data == null) {
                            throw (new Error("没有更多数据了！"))
                        }
                        this.PostList.push.apply(this.PostList, response.data.data)
                    }).catch(err => console.log(err))
            }

        },
    },
}
</script>
<style scoped>
.post_item {
    padding: 5px 0px;
}

.router-link-active {
    text-decoration: none;
}

a {
    text-decoration: none;
}

#post .user_info {
    display: flex;
}

#post .user_icon {
    width: 50px;
    height: 50px;
    margin-right: 10px;
    border-radius: 50px;
}

#post .post_img {
    width: 100% !important;
}

#post .content {
    display: flex;
}

#post .content {
    display: flex;
}

#post .time {
    color: gray;
    margin-right: 10px;
}

#post .forum {
    color: red;
}

#post .button {
    padding: 10px 90px 10px 90px;
    display: flex;
    justify-content: space-between;
}

#post .title {
    padding: 5px;
    font-size: large;
    font-weight: bolder;
    color: black;
}

#post .line {
    height: 5px;
    background-color: rgb(241, 236, 236);
    border-style: none;
    margin: 0;
}
</style>